<template>
  <div class="ym-confirm">
    <Ydialog v-model="value" @on-click-backdrop="OnClickBackdrop">
      <div class="ym-dialog__hd" v-if="title || $slots.title">
        <slot name="title">
          <span class="ym-dialog__title">{{ title }}</span>
        </slot>
      </div>
      <div class="ym-dialog__bd">
        <template v-if="showInput">
          <input type="text" class="ym-confirm__input" :placeholder="placeholder" v-model="currentInputValue">
        </template>
        <template v-else>
          <slot>
            <div v-html="content"></div>
          </slot>
        </template>
      </div>
      <div class="ym-dialog__ft">
        <slot name="footer">
          <a
            class="ym-dialog__btn ym-dialog__btn--clickable"
            @click="onCancleClick"
            v-if="showCancel"
            v-text="cancelText"></a>
          <a
            class="ym-dialog__btn ym-dialog__btn--primary ym-dialog__btn--clickable"
            @click="onConfirmClick"
            v-if="showConfirm"
            v-text="confirmText"></a>
        </slot>
      </div>
    </Ydialog>
  </div>
</template>

<script>
import Backdrop from '../backdrop'
import Ydialog from '../dialog'

export default {
  name: 'ym-confirm',
  components: {
    Ydialog,
    Backdrop
  },
  props: {
    value: Boolean,
    title: String,
    content: String,
    showInput: Boolean,
    placeholder: String,
    confirmText: {
      type: String,
      default: ''
    },
    cancelText: {
      type: String,
      default: ''
    },
    closeOnClickBackdrop: {
      type: Boolean,
      default: true
    },
    showCancel: {
      type: Boolean,
      default: true
    },
    showConfirm: {
      type: Boolean,
      default: true      
    },
    inputValue: {
      type: [String, Number],
      default: ''
    }
  },
  data () {
    return {
      currentInputValue: ''
    }
  },
  watch: {
    inputValue (val) {
      this.currentInputValue = val
    },
    currentInputValue (val) {
      this.$emit('on-input-change', val)
    }
  },
  methods: {
    OnClickBackdrop () {
      if (this.closeOnClickBackdrop) this.hide()
      this.$emit('on-click-backdrop')
    },
    hide () {
      this.$emit('input', false)
    },
    onCancleClick () {
      this.$emit('on-cancel')
    },
    onConfirmClick () {
      this.$emit('on-confirm')
    }
  },
  created () {
    if (typeof this.inputValue !== 'undefined') {
      this.currentInputValue = this.inputValue
    }
  }
}
</script>

<style lang="scss" src="./index.scss"></style>



